<template>
    <div>
        <button class="btn btn-outline-warning" @click="remove"><i class="fas fa-times"></i> remover</button>
        <button class="btn btn-outline-warning" @click="moveup"><i class="fas fa-arrow-up"></i> subir</button>
        <button class="btn btn-outline-warning" @click="movedown"><i class="fas fa-arrow-down"></i> bajar</button>
        <property v-for="(property, index) in properties" :key="index" v-model="properties[index]" :root="root" />
    </div>
</template>
<script>

    import Property from "./Property";
    import Inspector from "../mixins/Inspector";

    export default {
        components: {
            Property,
        },
        mixins: [Inspector],
        props: {
            value: null,
        },
        computed: {
            properties() {
                return this.getArray("Property");
            },
        },
        methods: {
            remove() {
                this.$store.dispatch('remove', {
                    target: this.value,
                });
                this.root.select(null, null);
            },
            moveup() {
                this.$store.dispatch('moveup', {
                    target: this.value,
                });
            },
            movedown() {
                this.$store.dispatch('movedown', {
                    target: this.value,
                });
            },
        },
    }
</script>
