<template>
    <drop class="form-contain" @dragover="handleDragover" @drop="handleDrop">
        <jdd-container v-for="(jddContainer, index) in jddContainers" :key="index" v-model="jddContainers[index]" :root="root" />
    </drop>
</template>
<script>

    import { Drop } from 'vue-drag-drop';
    import JddContainer from "./JddContainer";
    import CanContain from "../mixins/CanContain";
    import Configurable from "../mixins/Configurable";

    export default {
        components: {
            JddContainer,
            Drop
        },
        mixins: [CanContain, Configurable],
        props: {
            value: null,
        },
        computed: {
            jddContainers() {
                return this.getArray("JddContainer");
            },
        },
        methods: {
            handleDragover(data, event) {
                if (event.target === this.$el && data.tag !=='JddContainer') {
                    event.dataTransfer.dropEffect = 'none';
                }
            },
			handleDrop(data, event) {
                if (event.target === this.$el) {
                    const item = JSON.parse(JSON.stringify(data.content));
                    this.$store.dispatch('add', {
                        target: this.value,
                        place: 'JddContainer',
                        child: item,
                    });
                }
			},
        },
    }
</script>

<style scoped>
    .form-contain {
        border: 1px dashed gray;
        padding-bottom: 2em;
    }
    .form-contain:empty {
        border: 1px dashed gray;
        padding-bottom: 3em;
    }
</style>

