<template>
  <editor v-model="innerValue" @onChange="change" :api-key="apiKey"></editor>
</template>
<script>
import Editor from "@tinymce/tinymce-vue";
export default {
  mixins: [],
  components: {
    Editor
  },
  props: {
    value: null,
    apiKey: null,
  },
  data() {
    return {
      innerValue: ""
    };
  },
  watch: {
    value: {
      inmediate: true,
      handler(value) {
        if (value) {
          const parsed = JSON.parse(value);
          const newValue = parsed ? parsed.substr(5, parsed.length - 11) : "";
          this.innerValue != newValue ? (this.innerValue = newValue) : null;
        }
      }
    }
  },
  methods: {
    change() {
      this.$emit("input", JSON.stringify("<div>" + this.innerValue + "</div>"));
    }
  }
};
</script>
