<template>
    <component :is="component" @jdd-click="click" v-bind="configc" />
</template>
<script>

    import Configurable from "../mixins/Configurable";
    import Additionable from "../mixins/Additionable";
    import { components } from 'vue-jdd-components';

    export default {
        components,
        mixins: [Configurable, Additionable],
        props: {
            value: null,
        },
        computed: {
            component() {
                return this.value.component;
            },
            configc() {
                const config = {};
                const value = this.value;
                Object.keys(value.$).forEach(key => {
                    value.$[key] ? config[key] = JSON.parse(value.$[key]) : null;
                });
                return config;
            },
        },
        data() {
            return {
                config: {},
            };
        },
        methods: {
            click(event, element) {
                this.root.select(this.value.$, element);
            },
            refreshConfig(value) {
                const config = {};
                Object.keys(value.$).forEach(key => {
                    value.$[key] ? config[key] = JSON.parse(value.$[key]) : null;
                });
                this.$set(this, 'config', config);
            },
        },
        watch: {
            value: {
                inmediate: true,
                deep: true,
                handler(value) {
                    this.refreshConfig(value);
                },
            },
        },
    }
</script>
