<template>
    <div class="form-group px-2">
        <div class="inspector-label text-muted">{{value.label}}</div>
        <component :is="component" v-bind="configc" class="form-control" v-model="value.owner[value.key]" @input="change" />
    </div>
</template>
<script>
    import PropertyHtml from './PropertyHtml';

    export default {
        mixins: [],
        components: {
            PropertyHtml,
        },
        computed: {
            component() {
                return this.value.component instanceof String ? this.value.component : this.value.component.component;
            },
            configc() {
                return this.value.component.props instanceof Object ? this.value.component.props : {};
            },
        },
        props: {
            value: null,
        },
        data() {
            return {
                innerValue: '',
            };
        },
        methods: {
            change() {
                this.$store.dispatch('change', {
                    target: this.value.owner,
                    key: this.value.key,
                    value: this.value.owner[this.value.key],
                });
            },
        },
    }
</script>

<style scoped>
.inspector-label {
    position: absolute;
    right: 2em;
    text-align: right;
    margin-top: 0.5em;
}
</style>
