<template>
    <drag style="display: inline-block;" :transfer-data="value">
        <button class="btn btn-outline-secondary btn-sm">
            {{label}}
        </button>
    </drag>
</template>
<script>
    import { Drag } from 'vue-drag-drop';

    import PaleteItem from "../mixins/PaleteItem";

    export default {
        components: {
            Drag,
        },
        mixins: [PaleteItem],
        props: {
            value: null,
        },
        computed: {
            label() {
                return this.value.label
            },
        }
    }
</script>
