
export default {
    props: {
        root: null,
        element: null,
    },
    methods: {
        getArray(name) {
            if (name === 'Property') {
                const definition = this.root.palete.items.find(item =>
                    item.content.component === this.element.$options._componentTag
                );
                const properties = [];
                Object.keys(this.value).forEach(key => {
                    properties.push({
                        label: key,
                        key: key,
                        owner: this.value,
                        component: definition && definition.inspector &&  definition.inspector[key] ? definition.inspector[key] : 'input',
                    });
                });
                return properties;
            }
        },
        getItem(name) {
            return this.value[name];
        },
    },
};
