<template>
    <drop class="container-contain drop" @dragover="handleDragover" @drop="handleDrop" :class="containerClass"
        @dragleave="over = false" @click="select">
        <jdd-container v-for="(jddContainer, index) in jddContainers" :key="'container-' + index" v-model="jddContainers[index]" :root="root" />
        <jdd-control v-for="(jddControl, index) in jddControls" :key="'control-' + index" v-model="jddControls[index]" :root="root" />
    </drop>
</template>
<script>

    import { Drop } from 'vue-drag-drop';
    import CanContain from "../mixins/CanContain";
    import Configurable from "../mixins/Configurable";
    import Additionable from "../mixins/Additionable";
    import JddControl from "./JddControl";

    export default {
        components: {
            JddContainer: () => import("./JddContainer"),
            Drop,
            JddControl,
        },
        mixins: [CanContain, Configurable, Additionable],
        data() {
            return {
                over: false,
                selected: false,
            };
        },
        props: {
            value: null,
        },
        computed: {
            containerClass() {
                return this.value.class + (this.over ? ' over' : '') + (this.selected ? ' selected' : '');
            },
            jddContainers() {
                return this.getArray("JddContainer");
            },
            jddControls() {
                return this.getArray("JddControl");
            },
        },
        methods: {
            select() {
                this.selected = !this.selected;
                this.root.select(this.value.$, this);
            },
            handleDragover(data, event) {
                this.over = event.target === this.$el;
                if (event.target === this.$el && (data.tag !=='JddContainer' && data.tag !=='JddControl')) {
                    this.over = false;
                    event.dataTransfer.dropEffect = 'none';
                }
            },
			handleDrop(data) {
                this.over = false;
                if (event.target === this.$el && data.tag ==='JddContainer') {
                    const item = JSON.parse(JSON.stringify(data.content));
                    item.class = this.value.class === 'row' ? 'col' : 'row';
                    this.$store.dispatch('add', {
                        target: this.value,
                        place: 'JddContainer',
                        child: item,
                    });
                    event.stopPropagation();
                }
                if (event.target === this.$el && data.tag =='JddControl') {
                    const item = JSON.parse(JSON.stringify(data.content));
                    this.$store.dispatch('add', {
                        target: this.value,
                        place: 'JddControl',
                        child: item,
                    });
                    event.stopPropagation();
                }
			},
        },
        updated: function () {
            this.$nextTick(function () {
                //this.$el.removeEventListener(events.move, moveFn);
            })
        },
        mounted() {
            this.$el.addEventListener('click', event => {
                this.$el === event.target ? this.select() : null;
            });
        },
    }
</script>

<style scoped>
    .container-contain div {
        width: 100%;
    }
    .container-contain {
        border: 1px dashed gray;
        margin: 1em;
        position: relative;
    }
    .container-contain:empty {
        padding-bottom: 2em;
    }
    .over {
        background-color: rgba(119, 136, 153, 0.5);
    }
    .selected {
        background-color: rgba(0, 136, 153, 0.2);
    }
